"use client"

import { Box, Button, Card, CardContent, CardMedia, IconButton, Typography } from "@mui/material"
import { useRef } from "react"
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';

const flowers = [
  { name: "ვარდი", price: "15$", img: "roses.jpg" },
  { name: "შროშანი", price: "10$", img: "lily.jpg" },
  { name: "ტიტა", price: "12$", img: "tulip.jpg" },
  { name: "გვირილა", price: "11$", img: "daisy.jpg" },
  { name: "ქრიზანთემა", price: "9$", img: "Chrysanthemum.jpg" },
  { name: "მიხაკი", price: "18$", img: "Carnation.jpg" },
  { name: "ლავანდა", price: "6$", img: "Lavender.jpg" },
  { name: "ორქიდეა", price: "11$", img: "Orchid.jpg" },
  { name: "მზეაყვავილა", price: "12$", img: "Sunflower.jpg" },
]

const BestSellers = () => {
  const scrollRef = useRef<HTMLDivElement>(null)

  const scroll = (direction: "left" | "right") => {
    if (scrollRef.current) {
      const scrollAmount = direction === "left" ? -300 : 300
      scrollRef.current.scrollBy({ left: scrollAmount, behavior: "smooth" })
    }
  }

  return (
    <Box sx={{
      width: "100%",
      height: 600,
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      justifyContent: "center",
      gap: 5,
      position: "relative",
    }}>
      <Typography
        variant="h3"
        letterSpacing={3}
        sx={{ fontFamily: "cursive" }}
      >
        Best Sellers:
      </Typography>

      {/* Left Arrow */}
      <IconButton
      disableFocusRipple
      disableRipple
        onClick={() => scroll("left")}
        sx={{
          position: "absolute",
          left: 10,
          top: "50%",
          transform: "translateY(-50%)",
          zIndex: 2,
          backgroundColor: "white",
          boxShadow: 2,
          "&:hover": { backgroundColor: "#ffe6f0" }
        }}
      >
        <ArrowBackIcon sx={{fontSize: 40, p: 1}}/>
      </IconButton>

      {/* Scrollable Box */}
      <Box
        ref={scrollRef}
        sx={{
          display: "flex",
          overflowX: "auto",
          overflowY: "hidden",
          scrollbarWidth: "none",
          msOverflowStyle: "none",
          width: "90%",
          gap: 2,
          p: 1,
          scrollBehavior: "smooth",
          "&::-webkit-scrollbar": { display: "none" },
        }}
      >
        {flowers.map((item, index) => (
          <Card
            key={index}
            sx={{
              flexShrink: 0,
              width: 300,
              height: 400,
              borderRadius: 4,
              boxShadow: 4,
              transition: "0.3s ease",
              "&:hover": {
                transform: "scale(1.03)",
                boxShadow: 10,
              },
            }}
          >
            <CardMedia
              component="img"
              height="230"
              image={item.img}
              alt={item.name}
            />
            <CardContent>
              <Typography
                variant="h6"
                gutterBottom
                sx={{ fontWeight: "bold", fontFamily: "Georgia, serif" }}
              >
                {item.name}
              </Typography>

              <Box
                sx={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                }}
              >
                <Typography
                  variant="h6"
                  sx={{ color: "crimson", fontWeight: "bold", fontSize: "1.2rem" }}
                >
                  {item.price}
                </Typography>

                <Button
                  variant="outlined"
                  sx={{
                    borderColor: "pink",
                    color: "pink",
                    textTransform: "none",
                    fontWeight: "bold",
                    "&:hover": {
                      borderColor: "#ff4081",
                      backgroundColor: "#ffe6f0",
                      color: "#ff4081",
                    },
                  }}
                >
                  Buy
                </Button>
              </Box>
            </CardContent>
          </Card>
        ))}
      </Box>

      {/* Right Arrow */}
      <IconButton
      disableFocusRipple
      disableRipple
        onClick={() => scroll("right")}
        sx={{
          position: "absolute",
          right: 10,
          top: "50%",
          transform: "translateY(-50%)",
          zIndex: 2,
          backgroundColor: "white",
          boxShadow: 2,
          "&:hover": { backgroundColor: "#ffe6f0" }
        }}
      >
        <ArrowForwardIcon sx={{fontSize: 40, p: 1}}/>
      </IconButton>
    </Box>
  )
}

export default BestSellers
