"use client";
import { Grid } from "@mui/material";
import styles from "./page.module.css";
import FirstContent from "./components/firstContent/firstContent";
import MainContent from "./components/mainContent/MainContent";
import BestSellers from "./components/bestSellers/BestSellers";
import FriendlyMsg from "./components/friendlyMsg/FriendlyMsg";
import Header from "./components/header/Header"; // Import Header

import { useState } from "react";

// Flower type, so you can use it here for cart state type
type Flower = {
  name: string;
  price: string;
  img: string;
};

export default function Home() {
  const [cart, setCart] = useState<Flower[]>([]);
  const [cartOpen, setCartOpen] = useState(false);

  // Function to add flower to cart
  const handleAddToCart = (flower: Flower) => {
    setCart((prev) => [...prev, flower]);
  };

  const handleCartOpen = () => {
    setCartOpen(true);
  };

  const handleCartClose = () => {
    setCartOpen(false);
  };

  return (
    <>
      <Header cart={cart} onCartClick={handleCartOpen} onCloseCart={handleCartClose} cartOpen={cartOpen} />
      <Grid container>
        <Grid item xs={12} sx={{ pt: "90px" }}>
          <FirstContent />
        </Grid>

        <Grid item xs={9} sx={{ display: { xs: "none", md: "flex" }, height: "700px", m: "0 auto" }}>
          <MainContent onAddToCart={handleAddToCart} />
        </Grid>

        <Grid item xs={12}>
          <BestSellers />
        </Grid>

        <Grid item xs={9} sx={{ m: "0 auto" }}>
          <FriendlyMsg />
        </Grid>
      </Grid>
    </>
  );
}
