"use client";

import { useState } from "react";
import { Box, Button, Card, CardContent, CardMedia, Typography, Modal, Rating } from "@mui/material";
import styles from "./main.module.css";

type Flower = {
  name: string;
  price: string;
  img: string;
};

const flowers: Flower[] = [
  { name: "ვარდი", price: "15$", img: "roses.jpg" },
  { name: "შროშანი", price: "10$", img: "lily.jpg" },
  { name: "ტიტა", price: "12$", img: "tulip.jpg" },
  { name: "გვირილა", price: "11$", img: "daisy.jpg" },
  { name: "ქრიზანთემა", price: "9$", img: "Chrysanthemum.jpg" },
  { name: "მიხაკი", price: "18$", img: "Carnation.jpg" },
  { name: "ლავანდა", price: "6$", img: "Lavender.jpg" },
  { name: "ორქიდეა", price: "11$", img: "Orchid.jpg" },
  { name: "მზეაყვავილა", price: "17$", img: "Sunflower.jpg" },
  { name: "ნათმა", price: "15$", img: "Narcissus.jpg" },
];

interface MainContentProps {
  onAddToCart: (flower: Flower) => void;
}

export default function MainContent({ onAddToCart }: MainContentProps) {
  const [modalOpen, setModalOpen] = useState(false);
  const [selectedFlower, setSelectedFlower] = useState<Flower | null>(null);

  const openModal = (flower: Flower) => {
    setSelectedFlower(flower);
    setModalOpen(true);
  };

  const closeModal = () => {
    setModalOpen(false);
    setSelectedFlower(null);
  };

  return (
    <Box
      sx={{
        display: "grid",
        gridTemplateColumns: "repeat(auto-fill, minmax(250px, 1fr))",
        gap: 2,
        padding: 2,
      }}
    >
      {flowers.map((flower, index) => (
        <Card
          key={index}
          sx={{ maxWidth: 350, cursor: "pointer" }}
          onClick={() => openModal(flower)}
        >
          <CardMedia
            component="img"
            height="250"
            image={`/images/${flower.img}`}
            alt={flower.name}
          />
          <CardContent sx={{ textAlign: "center" }}>
            <Typography
              variant="h5"
              sx={{ fontFamily: "cursive", color: "purple" }}
            >
              {flower.name}
            </Typography>
            <Typography variant="body1">{flower.price}</Typography>
          </CardContent>
        </Card>
      ))}

      {/* Modal */}
      <Modal open={modalOpen} onClose={closeModal}>
        <Box
          sx={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            bgcolor: "background.paper",
            borderRadius: 3,
            boxShadow: 24,
            width: { xs: "90%", sm: 400 },
            p: 2,
          }}
        >
          {selectedFlower && (
            <>
              <Typography
                variant="h4"
                sx={{ fontFamily: "cursive", color: "purple", mb: 1 }}
              >
                {selectedFlower.name}
              </Typography>
              <Box
                component="img"
                src={`/images/${selectedFlower.img}`}
                alt={selectedFlower.name}
                sx={{
                  width: "100%",
                  borderRadius: 2,
                  mb: 1,
                }}
              />
              <Typography
                variant="body1"
                sx={{ mb: 2, fontWeight: "bold" }}
              >
                Price: {selectedFlower.price}
              </Typography>

              <Rating
                name="read-only"
                value={5}
                readOnly
                sx={{ mb: 2 }}
              />

              <Button
                variant="contained"
                onClick={() => {
                  onAddToCart(selectedFlower);
                  closeModal();
                }}
              >
                Add to Cart
              </Button>
            </>
          )}
        </Box>
      </Modal>
    </Box>
  );
}
